import { Component, Vue } from 'vue-property-decorator'
import {
  pageCompanyPayList,
  updateOffPayState,
} from '@/api/financail/financailApi'
import { getProjectList } from '@/api/manage/sys/houseManageApi'

@Component({
  components: {},
})
export default class check extends Vue {
  private pageTitle = '批量结算审核'
  // 参数
  private params = {
    company: '',
    current: 1,
    projectId: '',
    size: 10,
    state: '', // 1 未结算, 3已结算
    total: 0,
  }
  // 项目列表
  private projectList: any[] = []
  // table的数据源
  private dataSource: any[] = []
  /**
   * @Author roct
   * @Description 生命周期
   * @Date 22:02 2021/5/30 0030
   **/
  public mounted(): void {
    this.getPageCompanyPayList()
    this.requestProjectList()
  }
  /**
   * @Author roct
   * @Description 选择结算还是未结算
   * @Date 22:01 2021/5/30 0030
   **/
  // private change(val: number) {
  //   // console.log('val', val)
  //   this.getPageCompanyPayList()
  // }
  // /**
  //  * @Author roct
  //  * @Description 筛选项目改变
  //  * @Date 22:01 2021/5/30 0030
  //  **/
  // private selectChange() {
  //   this.getPageCompanyPayList()
  // }
  /**
   * @Author roct
   * @Description 修改页码
   * @Date 22:01 2021/5/30 0030
   **/
  private handleCurrentChange(e: number) {
    this.params.current = e
  }
  /**
   * @Author roct
   * @Description 获取批量审核列表
   * @Date 22:01 2021/5/30 0030
   **/
  private async getPageCompanyPayList() {
    const res = await pageCompanyPayList(this.params)
    this.params.total = res.data.total
    console.log('res', res)
    this.dataSource = res.data.records
  }
  /**
   * @Author roct
   * @Description 获取项目列表
   * @Date 22:01 2021/5/30 0030
   **/
  private async requestProjectList() {
    const res = await getProjectList()
    this.projectList = res.data
  }
  /**
   * @Author roct
   * @Description 点击结算
   * @Date 22:01 2021/5/30 0030
   **/
  private async toEndClick(scope: any) {
    try {
      await this.$confirm('是否结算?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
      await updateOffPayState({ mergeId: scope.row.id })
      await this.requestProjectList()
    } catch (e) {}
  }
}
